import React from "react"
import { useStaticQuery, graphql } from "gatsby"

import Link from "../components/helpers/link"

export default () => {
  const data = useStaticQuery(
    graphql`
      query {
        contributingToml {
          projects {
            name
            url
            filename
            description
          }
        }
      }
    `
  )
  const contributingData = data.contributingToml.projects
  const contributingEntries = []

  for (const [index, project] of contributingData.entries()) {
    contributingEntries.push(
      <div className="card card-sm card-body align-items-start rounded-sm my-2 mx-2" key={"contrib-"+index}>
        <div className="d-flex flex-row">
          <div className="col-2 p-0 text-center">
            <img src={"/assets/img/contributing/"+project.filename} alt={project.name} className="icon icon-lg mb-2" />
          </div>
          <div className="col-10 p-0 ml-2">
            <h5 className="card-title mb-2">
              <Link to={ project.url } className="pb-1">{ project.name }</Link>
            </h5>
            <p className="card-text">{ project.description }</p>
          </div>
        </div>
      </div>
    )
  }

  return (
  <section className="bg-primary-light has-divider">
    <div className="container pt-6">
      <div className="row justify-content-center text-center pb-0 mb-3">
        <div className="col-xl-8 col-lg-9">
          <h2 className="display-4 mx-xl-6">Contributions from Salesforce.</h2>
          <p className="lead">
            We contribute to thousands of projects every year, from key technologies powering our innovation to community projects that make our world a better place.
          </p>
        </div>
      </div>
      <div className="row justify-content-center">
        <div className="card-columns">
          { contributingEntries }
        </div>
      </div>
    </div>
  </section>
)}