import React from "react"
import classNames from "classnames"
import styles from "../styles/projects.module.css"

import Link from "../components/helpers/link"


export default ({ id, project }) => {
  const linkEntries = []

  for (const [index, link] of project.links.entries()) {
    linkEntries.push(
      <li key={ "project-"+id+"-link-"+index } className="list-inline-item mr-3">
        <Link to={ link.url } className={ classNames('pb-1', styles.link) }>
          <span className={classNames('oi', 'oi-'+link.icon, styles.icon)} title={link.name} aria-hidden="true"></span>
          { link.name }
        </Link>
      </li>
    )
  }

  return (
  <div className="col-md-6 col-lg-4 d-flex">
    <div className="card card-body rounded-sm justify-content-between" key={"project-"+id}>
      <div className="mb-1 mb-md-2">
        <img src={"/assets/img/projects/"+project.filename} alt={project.name} className={classNames('icon', styles.iconXl)} />
      </div>
      <div>
        <h4>{ project.name }</h4>
        <p>
          { project.description }
        </p>
        <ul className="list-inline">
          { linkEntries }
        </ul>
      </div>
    </div>
  </div>
)}