import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import classNames from "classnames"
import styles from "../styles/nav.module.css"

import Link from "./helpers/link"


export default () => {
  const data = useStaticQuery(
    graphql`
      query {
        githubToml {
          organizations {
            name
            url
          }
        }
      }
    `
  )

  const githubData = data.githubToml.organizations
  const githubEntries = []

  for (const [index, org] of githubData.entries()) {
    githubEntries.push(
      <Link to={ org.url } className="dropdown-item nav-link pl-2" key={"github-org-"+index}>{ org.name }</Link>
    )
  }

  return (
  <div className="navbar-container">
    <nav className="navbar navbar-expand-lg justify-content-between navbar-light border-bottom-0" data-sticky="top">
      <div className="container">
        <div className="col flex-fill px-0 d-flex justify-content-between">
          <Link to="/" className={ classNames('navbar-brand', 'mr-0', 'fade-page') }>
            <img src="/assets/img/salesforce-opensource.svg" alt="Salesforce Open Source" className={ styles.logo } />
          </Link>
          <button className="navbar-toggler" type="button" data-toggle="collapse" data-target=".navbar-collapse" aria-expanded="false" aria-label="Toggle navigation">
            <svg className="icon" width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path d="M3 17C3 17.5523 3.44772 18 4 18H20C20.5523 18 21 17.5523 21 17V17C21 16.4477 20.5523 16 20 16H4C3.44772 16 3 16.4477 3 17V17ZM3 12C3 12.5523 3.44772 13 4 13H20C20.5523 13 21 12.5523 21 12V12C21 11.4477 20.5523 11 20 11H4C3.44772 11 3 11.4477 3 12V12ZM4 6C3.44772 6 3 6.44772 3 7V7C3 7.55228 3.44772 8 4 8H20C20.5523 8 21 7.55228 21 7V7C21 6.44772 20.5523 6 20 6H4Z"
              fill="#212529" />
            </svg>
          </button>
        </div>
        <div className="collapse navbar-collapse col px-0 px-lg-2 flex-fill justify-content-end">
          <div className="py-2 py-lg-0">
            <ul className="navbar-nav">
              <li className="nav-item text-center">
                <Link to="https://engineering.salesforce.com/" className="nav-link">Blog</Link>
              </li>
              <li className="nav-item text-center">
                <Link to="https://twitter.com/salesforceeng" className="nav-link">Twitter</Link>
              </li>
              <li className="dropdown nav-item text-center">
                <Link to="#" className="nav-link dropdown-toggle" data-toggle="dropdown" aria-expanded="false" aria-haspopup="true">GitHub</Link>
                <div className="dropdown-menu m-0" aria-labelledby="dropdownMenuLink">
                  { githubEntries }
                </div>
              </li>
              <li className="nav-item text-center">
                <Link to="https://salesforce.wd1.myworkdayjobs.com/External_Career_Site/2/refreshFacet/318c8bb6f553100021d223d9780d30be?source=Salesforce_Open_Source_Site" className="nav-link">Careers</Link>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </nav>
  </div>
)}