import React from "react"
import { useStaticQuery, graphql } from "gatsby"

import Link from "../components/helpers/link"
import ProjectCard from "../components/projectcard"


export default () => {
  const data = useStaticQuery(
    graphql`
      query {
        projectsToml {
          projects {
            name
            category
            description
            filename
            links {
              name
              url
              icon
            }
          }
        }
      }
    `
  )
  const projectData = data.projectsToml.projects
  const projectEntries = []

  for (const [index, project] of projectData.entries()) {
    projectEntries.push(<ProjectCard key={index} project={project} />)
  }

  return (
  <section className="bg-primary has-divider">
    <div className="container pt-6">
      <div className="row justify-content-center text-center pb-0">
        <div className="col-xl-8 col-lg-9">
          <h2 className="display-4 mx-xl-6">Built at Salesforce.</h2>
          <p className="lead">
          We release projects, both big and small, that solve problems in infrastructure, security, application development, and artificial intelligence.
          </p>
        </div>
      </div>
      <div className="row justify-content-center text-center pb-6">
        <div className="col text-center">
          <Link to="https://github.com/salesforce" className="lead hover-arrow border-bottom-3 border-primary">See all our projects</Link>
        </div>
      </div>
      <div className="row justify-content-center">
        { projectEntries }
      </div>
    </div>
  </section>
)}