import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import * as moment from "moment"
import arraySort from "array-sort"
import classNames from "classnames"
import styles from "../styles/timeline.module.css"

import Link from "../components/helpers/link"


export default ( props ) => {
  const data = useStaticQuery(
    graphql`
      query {
        timelineToml {
          events {
            name
            description
            date
            project_url
            event_url
          }
        }
      }
    `
  )
  const timelineData = arraySort(data.timelineToml.events, 'date')
  const timelineEntries = []                 // Array of computed JSX elements
  const spacer = 14                          // Spacer is totalSize from ./src/components/graph.js
  const heights = [70, 210, 280, 140, 350]   // List of heights to cycle through to avoid visual collision
  const today = new moment()                 // Spacing starts at the begining of the timeline

  for (const [index, event] of timelineData.entries()) {
    const date  = moment(event.date, "YYYY-MM-DD")
    const between = moment.duration(today.diff(date))

    // Calculate spacing from today to the current event
    const spacerCount = Math.ceil(moment.duration(today.diff(date)).asDays())
    const margin = spacerCount * spacer

    const height = heights.shift() // Grab the next height off the list

    timelineEntries.push(
      <div className={classNames(styles.event, "border-left border-dark pl-1")} key={"event-"+index} style={{ marginLeft: margin, height: height }}>
        <h5 className="p-0 m-0">
          <Link to={ event.project_url } className={classNames(styles.name)}>
            { event.name }
          </Link>
        </h5>
        <p className={classNames("p-0 m-0", styles.description)}>
          <Link to={ event.event_url } className={classNames(styles.hoverArrow)}>
            { event.description }
          </Link>
        </p>
        <p className={classNames("p-0 m-0", styles.date)}>{ between.humanize() } ago</p>
      </div>
    )
    heights.push(height) // Put the used height on the end of the list
  }

  return (
    <div className="d-flex align-items-end pb-1">
      { timelineEntries }
    </div>
  )
}