import React from "react"
import _ from "lodash"
import { useStaticQuery, graphql } from "gatsby"


export default ( props ) => {
  const data = useStaticQuery(
    graphql`
      query {
        allContributionsCsv {
          edges {
            node {
              cnt
              dt
            }
          }
        }
      }
    `
  )
  const { edges: contributions } = data.allContributionsCsv
  const height = props.height  // Height of the header
  const div = 10                // Divide the contribution count into blocks
  const size = 12              // Size of the rectangle in px
  const padding = 2            // Space between rectangle in px
  const totalSize = size + padding

  const grid = []

  for (const [index, {node: contribution}] of contributions.entries()) {
    const x = index*(totalSize)
    let y = height-(totalSize)
    const repeat = Math.floor(contribution.cnt/div)

    _.times(repeat, (i) => {
      y = y - totalSize
      grid.push(<rect x={x} y={y} width={size} height={size} fill="#e0e0e0" key={index+"-"+i} />)
    })
  }

  return (
    <svg width="100%" height={ height } id='contribution-graph'>
      <title>Open Source Contributions</title>
      { grid }
    </svg>
  )
}