import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import classNames from "classnames"
import styles from "../styles/supporting.module.css"

import Link from "../components/helpers/link"


export default () => {
  const data = useStaticQuery(
    graphql`
      query {
        supportingToml {
          organizations {
            name
            url
            filename
          }
        }
      }
    `
  )
  const orgData = data.supportingToml.organizations
  const orgEntries = []

  for (const [index,org] of orgData.entries()) {
    orgEntries.push(
      <div key={"image-"+index} className="col-lg-2 col-md-4 col-6 d-flex mb-4 justify-content-center align-items-center">
        <div>
          <Link to={org.url} className={classNames(styles.orgLink)}>
            <img src={"/assets/img/supporting/"+org.filename} alt={org.name} className={classNames(styles.orgImage, 'px-1')} />
          </Link>
        </div>
      </div>
    )
  }
  return (
  <section className="bg-light-grey has-divider">
    <div className="container pt-6">
      <div className="row justify-content-center text-center pb-4">
        <div className="col-xl-8 col-lg-9">
          <h2 className="display-4 mx-xl-10">Supported by Salesforce.</h2>
          <p className="lead">
          Providing financial support to projects creates a healthy and thriving community. We’re proud supporters and partners with the following organizations.
          </p>
        </div>
      </div>
      <div className="row justify-content-center">
        { orgEntries }
      </div>
    </div>
  </section>
)}