import React from "react"
import { useStaticQuery, graphql } from "gatsby"

import Link from "../components/helpers/link"


export default () => {
  const data = useStaticQuery(
    graphql`
      query {
        blogsToml {
          blogs {
            title
            url
          }
        }
      }
    `
  )
  const blogData = data.blogsToml.blogs.slice(0, 3)
  const blogEntries = []

  for (const [index,blog] of blogData.entries()) {
    blogEntries.push(
      <li key={"blog-"+index} className="mb-2">
        <Link to={blog.url} className="lead">{blog.title}</Link>
      </li>
    )
  }

  return (
  <div className="col-md-6 d-flex mb-4">
    <div className="recentblogs">
      <h4 className="mb-3">
        <Link to="https://engineering.salesforce.com/tagged/open-source" className="hover-arrow border-bottom-3 border-primary">Recent Blogs</Link>
      </h4>
      <ul className="list-unstyled">
        { blogEntries }
      </ul>
    </div>
  </div>
)}